.App-header {
  position: relative;
  margin-left: 2em;
  gap: 2em;
  margin-top: 0.5em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em;
  font-size: 0.6rem;
  border-bottom: 1px solid #fffff2;

}


nav {
  display: flex;
  align-items: center;
}

/* clean up this */
.navlink {
  color: #fffff2;
  margin-right: 10px;
  text-decoration: none;
  transition: transform 0.3s ease-in-out;
}

.navlink:hover {
  cursor: pointer;
  transform: scale(1.1);
}

button {
  display: flex;
  align-items: center;
  border: none;
  background: none;
  cursor: pointer;
}

button svg {
  fill: white;
  width: 1rem;
  height: 1rem;
}

@media (max-width: 768px) {
  .App-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5em;
  }
}
