.Hero {
  font-family: 'Migra Extra Bold';
  font-size: 6rem;
  margin: 1rem;
  user-select: none;
  text-align: center;
  letter-spacing: 5px; 
  border-bottom: 1px solid #fffff2;

}

@media (max-width: 1024px) {
  .Hero {
      font-size: 2.5em;
      letter-spacing: 3px; 
  }
}

@media (max-width: 768px) {
  .Hero {
      font-size: 1em;
      letter-spacing: 1px; 
  }
}
