@font-face {
  font-family: "Migra Extra Bold";
  src: url("fonts/Migra-Extrabold.ttf") format("truetype");
}

@font-face {
  font-family: "Neue Montreal";
  src: url("fonts/PPNeueMontreal-Medium.otf") format("opentype");
}

@font-face {
  font-family: "Neue Montreal Bold";
  src: url("fonts/PPNeueMontreal-Bold.otf") format("opentype");
}



html,
body {
  margin-left: 0;
  padding: 0;
  font-family: "Neue Montreal Bold";
  color: #fffff2;
}


* {
  scrollbar-width: thin;
  scrollbar-color: #fffff2 darkgrey;
}

#particle-system-container {
  position: fixed;
  top: 0;
  z-index: -1;
}


.splash-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center; 
  align-items: center;
  background-color: #fffff2; 
  box-sizing: border-box;
  border: 0.3rem solid transparent; 
  border-image: url('assets/gold.jpg') 30 repeat; 
  z-index: 9999;
}

.splash-screen-inner {
  display: flex;
  flex-direction: row; 
  justify-content: center;
  align-items: center; 
  padding: 2rem; 
  box-sizing: border-box;
  border: 0.3rem solid transparent; 
  border-image: url('assets/gold.jpg') 20 repeat; 
}


.splash-screen h1.cross {
  font-size: 5em; 
  color: transparent;
  background-image: url('assets/gold.jpg'); 
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin: 0 20px; 

}


.splash-screen button {
  padding: 10px 20px;
  background-color: #fffff2; 
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 2rem; 
  font-family: 'Migra Extra Bold'; 
  z-index: 2; 
}

.splash-screen-inner:hover h1.cross {
  background-size: contain; 
}


.no-scroll {
  overflow: hidden;
}


.mobile-message{
  font-family: 'Migra Extra Bold';
  color: black;
}

  
@media (max-width: 1024px) { 
  .splash-screen h1.cross {
    font-size: 3rem;
  }

  .splash-screen button{
    font-size: 1.5rem;
  }
}

@media (max-width: 768px) { 
  .splash-screen h1.cross {
    font-size: 1.5rem;
  }

  .splash-screen button{
    font-size: 1rem;
  }
}