.Contact {
    margin: 5rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2.5rem;
    text-align: center;

  }
  
  
  .contact-details {
    display: flex;
    flex-direction: row; 
    align-items: center; 
    gap: 2rem; 
  }

  
  @media (max-width: 1024px) { 
    .Contact {
      font-size: 1.5rem;
    }
  }
  
  @media (max-width: 768px) { 
    .Contact {
      font-size: 1rem;
    }
}